@import '../../styles/variables.scss';

.contacts {
    margin: 0 10px;
    width: calc(100vw - 20px);
    height: 100%;
    color: white;
    padding: 20px;
    &_text {
        font-size: 16px;
        text-align: center;
        text-shadow: 1px 1px 1px black;
    }
    &_block {
        width: 100%;
    }
    &_link {
        display: block;
        margin: 70px auto 0;
        padding: 8px;
        height: 45px;
        width: 500px;
        border-radius: 5px;
        text-align: center;
        opacity: .8;
        background: $globalBlockColor;
        transition: .2s;
        cursor: pointer;
        &_text {
            color: white;
            font-size: 24px;
            display: inline-block;
            transform: translate(10px, -7px);
            text-shadow: 1px 1px 1px black;
            // user-select: all;
        }
        &_logo {
            height: 100%;
        }
    }
}

@media (max-height: 500px) {
    .contacts_block {
        display: flex;
        justify-content: space-around;
    }
    .contacts_link {
        margin-top: 30px;
    }
    .contacts_text {
        font-size: 12px;
    }
}

@media (max-width: 1200px) {
    .contacts_link {
        height: 30px;
        width: 200px;
        &:hover {
            width: 350px;
        }
    }
    .contacts_link_text {
        font-size: 12px;
        transform: translate(10px, -4px);
    }
    .contacts_text {
        font-size: 12px;
    }
}

@media (min-width: 1200px) {
    .contacts_link {
        &:hover {
            width: 650px;
        }
    }
}